import React, { Component } from "react";
import { Progress } from "reactstrap";

class Advanced extends Component {
  constructor(props) {
    super(props);

    this.displayMilestones = [];
    this.paymentStructure = React.createRef();

    this.state = {
      paymentStructure: "Pre Invoice",
      numberOfPayments: 1,
      validCombination: true,
      milestonePreInvoice: false,
      milestonePostInvoice: false,
      showMilestones: this.displayMilestones,
      totalAmount: 0,
      //prePaymentAmount: 0,
      //postPaymentAmount: 0,
      milestoneAmount: 0
    };
    this.handleMilestonePreChange = this.handleMilestonePreChange.bind(this);
    this.handleMilestonePostChange = this.handleMilestonePostChange.bind(this);
    this.handlePaymentChange = this.handlePaymentChange.bind(this);
    this.appendMilestone = this.appendMilestone.bind(this);
    this.removeMilestone = this.removeMilestone.bind(this);
    this.updateTotalAmount = this.updateTotalAmount.bind(this);
  }

  updateTotalAmount = e => {
    const { name, value } = e.target;
    const { prePaymentAmount, postPaymentAmount, milestoneAmount } = this.state;

    this.setState({
      [name]: value,
      totalAmount:
        parseInt(prePaymentAmount) +
        parseInt(postPaymentAmount) +
        parseInt(milestoneAmount)
    });
  };

  removeMilestone() {
    //var array = [...this.state.showMilestones];
    var index;
    index = this.displayMilestones.indexOf(index);
    this.displayMilestones.splice(index, 1);

    this.setState({ showMilestones: this.displayMilestones });
  }

  appendMilestone() {
    this.displayMilestones.push(
      <div className="form-group" id="display-milestones">
        <label htmlFor="milestoneName">Milestone Name</label>
        <input
          type="text"
          className="form-control"
          id="milestoneName"
          name="milestoneName"
          placeholder=""
        />
        <label htmlFor="milestoneAmount">Milestone Amount</label>
        <input
          type="number"
          className="form-control"
          id="milestoneAmount"
          name="milestoneAmount"
          placeholder=""
          onChange={this.updateTotalAmount}
        />
      </div>
    );
    this.setState({
      showMilestones: this.displayMilestones
    });
  }

  handlePaymentChange() {
    //this.setState({ paymentType: e.target.value });

    this.setState({
      paymentStructure: this.paymentStructure.current.value
    });
  }

  handleMilestonePreChange() {
    //this.setState({ paymentType: e.target.value });

    this.setState({
      milestonePreInvoice: !this.state.milestonePreInvoice
    });
  }

  handleMilestonePostChange() {
    //this.setState({ paymentType: e.target.value });

    this.setState({
      milestonePostInvoice: !this.state.milestonePostInvoice
    });
  }

  saveAndContinue = e => {
    e.preventDefault();

    this.props.nextStep();
  };

  previousStep = e => {
    e.preventDefault();

    this.props.previousStep();
  };

  render() {
    return (
      <div className="row m-30">
        <div className="col-md-3 border border-dark">
          <br />
          <div className="progress-bar">
            <Progress animated color="success" value={80} />
          </div>
          <br />
          <ul className="list-unstyled">
            <li>
              <i className="fas fa-check-circle" /> PreInvoice Terms of Service
            </li>
            <li>
              <i className="fas fa-check-circle" /> Basic Information
            </li>
            <li>
              <i className="fas fa-check-circle" /> Customer Data
            </li>
            <li>
              <i className="fas fa-check-circle" /> Advanced
            </li>
            <li>
              <i className="far fa-check-circle" /> Review
            </li>
          </ul>
        </div>

        <div className="col-md-7 border border-dark">
          <form id="msform">
            <fieldset>
              <h3>Advanced Details</h3>
              <h4>Deliverables</h4>
              <div className="form-group">
                <label htmlFor="deliverableAmount">Deliverable Amount</label>
                <input
                  type="text"
                  className="form-control"
                  id="deliverableAmount"
                  placeholder=""
                />
              </div>
              <div className="form-group">
                <label htmlFor="deliverableUnit">Deliverable Unit</label>
                <input
                  type="text"
                  className="form-control"
                  id="deliverableUnit"
                  placeholder=""
                />
              </div>
              <br />
              <br />
              <h4>Payment Details</h4>
              <div className="form-group">
                <label htmlFor="paymentStructure">Payment Structure</label>
                {"  "}
                <select
                  ref={this.paymentStructure}
                  onChange={this.handlePaymentChange}
                  name="paymentStructure"
                >
                  <option selected value="Pre Invoice">
                    Pre Invoice
                  </option>
                  <option value="Post Invoice">Post Invoice</option>
                  <option value="Milestone">Milestone Based</option>
                </select>
              </div>
              {this.state.paymentStructure !== "Milestone" ? (
                <div className="form-group">
                  <label htmlFor="singlePaymentAmount">
                    Invoice Payment Amount
                  </label>
                  <input
                    type="number"
                    className="form-control"
                    id="singlePaymentAmount"
                    placeholder=""
                    name="singlePaymentAmount"
                  />
                </div>
              ) : null}
              {this.state.paymentStructure === "Milestone" ? (
                <div className="form-group">
                  <h3>Milestone Setup</h3>
                  <div className="form-group">
                    <label>Pre Invoice</label>
                    <input
                      name="preInvoiceCheck"
                      type="checkbox"
                      checked={this.state.milestonePreInvoice}
                      onChange={this.handleMilestonePreChange}
                    />
                  </div>
                  {this.state.milestonePreInvoice ? (
                    <div className="form-group">
                      <label htmlFor="prePaymentAmount">
                        Pre Invoice Payment Amount
                      </label>
                      <input
                        type="number"
                        className="form-control"
                        id="prePaymentAmount"
                        name="prePaymentAmount"
                        placeholder=""
                        onChange={this.updateTotalAmount}
                      />
                    </div>
                  ) : null}

                  <div className="form-group">
                    <label>Post Invoice</label>
                    <input
                      name="postInvoiceCheck"
                      type="checkbox"
                      checked={this.state.milestonePostInvoice}
                      onChange={this.handleMilestonePostChange}
                    />
                  </div>
                  {this.state.milestonePostInvoice ? (
                    <div className="form-group">
                      <label htmlFor="postPaymentAmount">
                        Post Invoice Payment Amount
                      </label>
                      <input
                        type="number"
                        className="form-control"
                        id="postPaymentAmount"
                        name="postPaymentAmount"
                        placeholder=""
                        onChange={this.updateTotalAmount}
                      />
                    </div>
                  ) : null}

                  <h4>Middle of Project Milestones</h4>
                  <button
                    type="button"
                    className="btn btn-info action-button"
                    onClick={this.appendMilestone}
                  >
                    Add New Milestone
                  </button>
                  <button
                    type="button"
                    className="btn btn-danger action-button"
                    onClick={this.removeMilestone}
                  >
                    Remove Last Milestone
                  </button>
                  <div className="display-data-Container">
                    {this.displayMilestones}
                  </div>
                </div>
              ) : null}
              <button
                type="submit"
                className="btn btn-success action-button"
                onClick={this.previousStep}
              >
                Previous
              </button>{" "}
              <button
                type="submit"
                className="btn btn-success action-button"
                onClick={this.saveAndContinue}
              >
                Next
              </button>
              <br />
              <br />
              <br />
              <br />
            </fieldset>
          </form>
        </div>
        <div className="col-md-2 border border-dark">
          This is where helpful hints go as the user is filling out the form.
        </div>
      </div>
    );
  }
}

export default Advanced;
