import React, { Component } from "react";
import "../../App.css";
import Advanced from "./Advanced";
import BasicInfo from "./BasicInfo";
import TOS from "./TOS";
import CustomerData from "./CustomerData";
import Review from "./Review";

var formValues = {
  tosCheck: false,
  title: null,
  startDate: null,
  endDate: null,
  currency: null,
  firstName: null,
  lastName: null,
  email: null,
  primaryPhone: null,
  secondaryPhone: null,
  address: null,
  city: null,
  country: null,
  postalCode: null
};

class NewContract extends Component {
  constructor(props) {
    super(props);
    this.state = {
      step: 1,

      tosCheck: false,
      contractTitle: null,
      startDate: null,
      endDate: null,
      currency: null,
      customerFirstName: null,
      customerLastName: null,
      customerEmail: null,
      customerPrimaryPhone: null,
      customerSecondaryPhone: null,
      customerAddress: null,
      customerCity: null,
      customerCountry: null,
      customerPostalCode: null,
      deliverableAmount: 0,
      deliberableUnit: null,
      paymentStructure: null,
      singlePaymentAmount: 0,
      preInvoiceCheck: null,
      prePaymentAmount: 0,
      postInvoiceCheck: null,
      postPaymentAmount: 0
    };
  }

  saveValues = fields => {
    return (formValues = Object.assign({}, formValues, fields));
  };

  nextStep = () => {
    this.setState({
      step: this.state.step + 1
    });
  };

  previousStep = () => {
    this.setState({
      step: this.state.step - 1
    });
  };

  render() {
    switch (this.state.step) {
      case 1:
        return (
          <div>
            <h3>Create a Contract</h3>
            <TOS
              formValues={formValues}
              nextStep={this.nextStep}
              saveValues={this.saveValues}
            />
          </div>
        );
      case 2:
        return (
          <div>
            <h3>Create a Contract</h3>
            <BasicInfo
              formValues={formValues}
              nextStep={this.nextStep}
              previousStep={this.previousStep}
              saveValues={this.saveValues}
            />
          </div>
        );
      case 3:
        return (
          <div>
            <h3>Create a Contract</h3>
            <CustomerData
              formValues={formValues}
              nextStep={this.nextStep}
              previousStep={this.previousStep}
              saveValues={this.saveValues}
            />
          </div>
        );
      case 4:
        return (
          <div>
            <h3>Create a Contract</h3>
            <Advanced
              formValues={formValues}
              nextStep={this.nextStep}
              previousStep={this.previousStep}
              saveValues={this.saveValues}
            />
          </div>
        );
      case 5: //review
        return (
          <Review
            formValues={formValues}
            previousStep={this.previousStep}
            saveValues={this.saveValues}
          />
        );
      case 6: //review
        return (
          <Review
            formValues={formValues}
            previousStep={this.previousStep}
            saveValues={this.saveValues}
          />
        );
      default:
        return <h3>Default</h3>;
    }
  }
}
export default NewContract;
