import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import { auth, db } from "../../store";

const byPropKey = (propertyName, value) => () => ({
  [propertyName]: value
});

class Register extends Component {
  constructor(props) {
    super(props);
  }

  state = {
    email: "",
    passord1: "",
    password2: "",
    error: null
  };

  onSubmit = e => {
    const { email, password1 } = this.state;

    auth
      .doCreateUserWithEmailAndPassword(email, password1)
      .then(authUser => {
        db.doCreateUser(authUser.user.uid, email)
          .then(() => {
            this.setState(() => ({
              email: this.state.email,
              password: this.state.password1
            }));
          })
          .catch(error => {
            this.setState(byPropKey("error", error));
          });
      })
      .catch(error => {
        this.setState(byPropKey("error", error));
      });

    e.preventDefault();
    this.props.history.push("/login");
  };

  render() {
    const { email, password1, password2, error } = this.state;
    const isInvalid =
      password1 !== password2 || password1 === "" || email === "";
    return (
      <div className="row">
        <div className="col-md-12">
          <Link to="/login" className="btn btn-link">
            <i className="fas fa-arrow-circle-left" /> Back to Dashboard
          </Link>
        </div>
        <div className="col-md-12 border border-dark mx-auto">
          <h2>Register An Account</h2>
          <form id="signupform" onSubmit={this.onSubmit}>
            <fieldset>
              <div className="form-group">
                <label htmlFor="email">Email Address</label>
                <input
                  type="text"
                  className="form-control"
                  id="email"
                  name="email"
                  placeholder=""
                  onChange={e =>
                    this.setState(byPropKey("email", e.target.value))
                  }
                  value={email}
                />
              </div>
              <div className="form-group">
                <label htmlFor="password1">Password</label>
                <input
                  type="password"
                  className="form-control"
                  id="password1"
                  name="password1"
                  placeholder=""
                  onChange={e =>
                    this.setState(byPropKey("password1", e.target.value))
                  }
                  value={password1}
                />
              </div>
              <div className="form-group">
                <label htmlFor="password2">Retype Password</label>
                <input
                  type="password"
                  className="form-control"
                  id="password2"
                  name="password2"
                  placeholder=""
                  onChange={e =>
                    this.setState(byPropKey("password2", e.target.value))
                  }
                  value={password2}
                />
              </div>
              <button
                disabled={isInvalid}
                type="submit"
                className="btn btn-primary btn-block"
              >
                Sign Up
              </button>
              {error && <p>{error.message}</p>}
              <hr />
              <center>
                You will be required to verify your email address. Once you are
                logged, you will be access to verify your identity before you
                can create a contract and gain full access to the platform.
                <br />
              </center>
            </fieldset>
          </form>
        </div>
      </div>
    );
  }
}

export default withRouter(Register);
