import React, { Component } from "react";
import { Progress } from "reactstrap";

class BasicInfo extends Component {
  saveAndContinue = e => {
    e.preventDefault();
    this.props.nextStep();
  };

  previousStep = e => {
    e.preventDefault();

    this.props.previousStep();
  };

  onChange = e => {
    /*
      Because we named the inputs to match their
      corresponding values in state, it's
      super easy to update the state
    */
    this.setState({ [e.target.name]: e.target.value });
  };

  render() {
    return (
      <div className="row m-30">
        <div className="col-md-3 border border-dark">
          <br />
          <div className="progress-bar">
            <Progress animated color="success" value={40} />
          </div>
          <br />
          <ul className="list-unstyled">
            <li>
              <i className="fas fa-check-circle" /> PreInvoice Terms of Service
            </li>
            <li>
              <i className="fas fa-check-circle" /> Basic Information
            </li>
            <li>
              <i className="far fa-check-circle" /> Customer Data
            </li>
            <li>
              <i className="far fa-check-circle" /> Advanced
            </li>
            <li>
              <i className="far fa-check-circle" /> Review
            </li>
          </ul>
        </div>

        <div className="col-md-7 border border-dark">
          <form id="msform">
            <fieldset>
              <h3>Basic Information</h3>
              <div className="form-group">
                <label htmlFor="contractTitle">Contract Title / Name</label>
                <input
                  type="text"
                  className="form-control"
                  id="contractTitle"
                  name="contractTitle"
                  placeholder="TITLE"
                  onChange={this.onChange}
                />
              </div>
              <div className="form-group">
                <label htmlFor="startDate">Start Date</label>
                <input
                  type="date"
                  className="form-control"
                  id="startDate"
                  name="startDate"
                  onChange={this.onChange}
                />
              </div>
              <div className="form-group">
                <label htmlFor="endDate">End Date</label>
                <input
                  type="date"
                  className="form-control"
                  id="endDate"
                  name="endDate"
                  onChange={this.onChange}
                />
              </div>
              <div className="form-group">
                <label htmlFor="currency">Contract Currency</label>
                {"  "}
                <select name="currency" id="currency" onChange={this.onChange}>
                  <option value="CAD">CAD</option>
                  <option value="USD">USD</option>
                </select>
              </div>
              <button
                type="submit"
                className="btn btn-success action-button"
                onClick={this.previousStep}
              >
                Previous
              </button>{" "}
              <button
                type="submit"
                className="btn btn-success action-button"
                onClick={this.saveAndContinue}
              >
                Next
              </button>
              <br />
              <br />
            </fieldset>
          </form>
        </div>
        <div className="col-md-2 border border-dark">
          This is where helpful hints go as the user is filling out the form.
        </div>
      </div>
    );
  }
}

export default BasicInfo;
