import React, { Component } from "react";
import { Progress } from "reactstrap";
import Alert from "../layout/Alert";
import { notifyUser } from "../../actions/notifyActions";
import { compose } from "redux";
import { connect } from "react-redux";

class TOS extends Component {
  constructor(props) {
    super(props);

    this.state = { tosCheck: false };
  }

  toggle(event) {
    this.setState({ tosCheck: !this.state.tosCheck });
  }

  saveAndContinue = e => {
    e.preventDefault();
    this.props.nextStep();
  };

  render() {
    //const { formValue, notifyUser } = this.props;
    //const { message, messageType } = this.props.notify;
    const { tosCheck } = this.state;

    return (
      <div className="row m-30">
        <div className="col-md-3 border border-dark">
          <br />
          <div className="progress-bar">
            <Progress animated color="success" value={20} />
          </div>
          <br />
          <ul className="list-unstyled">
            <li>
              <i className="fas fa-check-circle" /> PreInvoice Terms of Service
            </li>
            <li>
              <i className="far fa-check-circle" /> Basic Information
            </li>
            <li>
              <i className="far fa-check-circle" /> Customer Data
            </li>
            <li>
              <i className="far fa-check-circle" /> Advanced
            </li>
            <li>
              <i className="far fa-check-circle" /> Review
            </li>
          </ul>
        </div>

        <div className="col-md-7 border border-dark">
          <form id="msform">
            <fieldset>
              <h3>PreInvoice Terms of Service</h3>
              This is where the Terms and Services would go. A supplier must
              acknowledge that they have read and agree before proceeding on to
              create a contract.
              <br />
              <br />
              <div className="form-group">
                <label htmlFor="TOS">I accept the terms and conditions</label>
                <input
                  type="checkbox"
                  className="form-control"
                  name="tosCheck"
                  id="tosCheck"
                  defaultValue={tosCheck}
                  onClick={this.toggle.bind(this)}
                />
              </div>
              <br />
              <br />
              {tosCheck ? null : (
                <Alert
                  message="You must accept the terms and conditions before continuing!"
                  messageType="error"
                />
              )}
              <input
                type="button"
                name="next"
                className="next btn btn-success action-button"
                value="Next"
                onClick={tosCheck ? this.saveAndContinue : null}
              />
              <br />
              <br />
            </fieldset>
          </form>
        </div>
        <div className="col-md-2 border border-dark">
          This is where helpful hints go as the user is filling out the form.
        </div>
      </div>
    );
  }
}

export default compose(
  connect(
    state => ({
      notify: state.notify
    }),
    { notifyUser }
  )
)(TOS);

