import React, { Component } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import { UserIsAuthenticated, UserIsNotAuthenticated } from "./helpers/auth";

import { Provider } from "react-redux";
import store from "./store";

import AppNavbar from "./components/layout/AppNavbar";
import Dashboard from "./components/layout/Dashboard";
import AddClient from "./components/clients/AddClient";
import EditClient from "./components/clients/EditClient";
import Login from "./components/auth/Login";
import Settings from "./components/settings/Settings";
import ClientDetails from "./components/clients/ClientDetails";
import Profile from "./components/clients/Profile";
import Contracts from "./components/contracts/Contracts";
//import Register from "./components/auth/Register";
import "./App.css";
import ContractDetails from "./components/contracts/ContractDetails";
import NewContract from "./components/contracts/NewContract";
import ForgotPW from "./components/auth/ForgotPW";
import Register from "./components/auth/Register";

class App extends Component {
  render() {
    return (
      <Provider store={store}>
        <Router>
          <div className="App">
            <AppNavbar />
            <div className="container">
              <Switch>
                <Route
                  exact
                  path="/"
                  component={UserIsAuthenticated(Dashboard)}
                />
                <Route
                  exact
                  path="/client/add"
                  component={UserIsAuthenticated(AddClient)}
                />
                <Route
                  exact
                  path="/client/forgotpw"
                  component={UserIsNotAuthenticated(ForgotPW)}
                />

                <Route
                  exact
                  path="/client/:id"
                  component={UserIsAuthenticated(ClientDetails)}
                />
                <Route
                  exact
                  path="/profile/:id"
                  component={UserIsAuthenticated(Profile)}
                />

                <Route
                  exact
                  path="/client/edit/:id"
                  component={UserIsAuthenticated(EditClient)}
                />
                <Route
                  exact
                  path="/login"
                  component={UserIsNotAuthenticated(Login)}
                />
                <Route
                  exact
                  path="/register"
                  component={UserIsNotAuthenticated(Register)}
                />
                <Route
                  exact
                  path="/settings"
                  component={UserIsAuthenticated(Settings)}
                />
                <Route
                  exact
                  path="/contracts"
                  component={UserIsAuthenticated(Contracts)}
                />
                <Route
                  exact
                  path="/contractdetails/:id"
                  component={UserIsAuthenticated(ContractDetails)}
                />
                <Route
                  exact
                  path="/newcontract"
                  component={UserIsAuthenticated(NewContract)}
                />
              </Switch>
            </div>
          </div>
        </Router>
      </Provider>
    );
  }
}

export default App;
